<template>
  <div>
    <v-row>
      <v-col cols="3">
        <v-card raised outlined>
          <v-card-text class="text-left black--text body-1">
            <strong>Homework Name:</strong> {{ homework.name }}
            <br />
            <strong>Student Name:</strong> {{ student.name }}
            <br />
            <strong>Sent At:</strong> {{ assignment.updatedAt }}
            <br />
            <strong>Mark:</strong> {{ assignment.mark }}
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card outlined raised>
          <v-card-text
            class="text-left black--text body-1"
            v-if="homework.questions"
          >
            <strong>Question Count:</strong> {{ homework.questions.length }}
            <br />
            <strong>Correct:</strong> {{ correctCount }}
            <br />
            <strong>False:</strong> {{ falseCount }}
            <br />
            <strong>Average:</strong>
            {{ average }}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-btn class="ma-2" color="primary" @click="completeReview">
      <v-icon class="mr-1">mdi-content-save</v-icon>
      Save
    </v-btn>
    <v-divider></v-divider>
    <v-row>
      <v-col
        cols="6"
        v-for="(question, index) in homework.questions"
        :key="index"
      >
        <v-card raised outlined >
          <v-card-title>
            <v-row class="text-left headline font-weight-black">
              <v-col cols="10"
                ><span class="font-weight-light">Question: </span
                >{{ homework.questions[index].text }}</v-col
              >
              <v-col cols="2" class="headline pa-0">
                <v-textarea
                  rows="1"
                  no-resize
                  outlined
                  v-if="!question.answerType == 'multiple'"
                  v-model.number="assignment.points[index]"
                  type="number"
                  class="green--text ma-0"
                >
                </v-textarea>
                <span
                  class="red--text"
                  v-else
                  :class="{
                    'green--text': assignment.isCorrect[index] == true
                  }"
                >
                  {{ assignment.points[index] }} / {{ question.point }}
                </span>
                <v-icon
                  v-if="assignment.isCorrect[index] == true"
                  large
                  color="green"
                  >mdi-check-circle
                </v-icon>
                <v-icon
                  v-else-if="assignment.isCorrect[index] == false"
                  large
                  color="red"
                  >mdi-close-circle
                </v-icon>
              </v-col>
            </v-row>
          </v-card-title>

          <v-card-text
            class="text-left font-weight-black headline"
            :class="{
              'green--text': assignment.points[index] > 0,
              'red--text': assignment.isCorrect[index] == false
            }"
          >
            <div v-if="question.choices.length > 0">
              <v-radio-group v-model="question.answer">
                <v-radio
                  disabled
                  v-for="(choice, index) in question.choices"
                  :key="index"
                  :label="choice"
                ></v-radio>
              </v-radio-group>
              <v-divider></v-divider>
              <span class="font-weight-light">Student answer: </span>
              {{ question.choices[assignment.answers[index]] }}
            </div>

            <div v-else>
              <v-divider></v-divider>
              <span class="font-weight-light">Student answer: </span>
              {{ assignment.answers[index] }}
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import StudentService from "../services/StudentService";
import HomeworkService from "../services/HomeworkService";

export default {
  data() {
    return {
      assignmentId: Number,
      homeworkId: Number,
      studentId: Number,
      assignment: {},
      correctCount: 0,
      falseCount: 0,
      homework: {},
      student: {},
      average: Number
    };
  },
  created() {
    this.assignmentId = this.$route.params.assignmentId;

    HomeworkService.GetAssignment(this.assignmentId).then(assignment => {
      this.assignment = assignment;
      this.homeworkId = assignment.homeworkId;
      this.studentId = assignment.studentId;

      for (let i = 0; i < assignment.isCorrect.length; i++) {
        const el = assignment.isCorrect[i];
        if (el == true) {
          this.correctCount++;
        } else if (el == false) {
          this.falseCount++;
        }
      }

      StudentService.GetStudent(this.studentId).then(student => {
        this.student = student;
      });

      HomeworkService.GetHomework(this.homeworkId).then(homework => {
        this.homework = homework;
      });

      HomeworkService.GetAssignments(this.homeworkId).then(assignments => {
        const filtered = assignments.filter(el => el.isReviewed == true);
        let count = filtered.length;
        let sum = 0;

        for (let i = 0; i < count; i++) {
          const el = filtered[i];
          sum += el.mark;
        }

        this.average = sum / count;
      });
    });
  },

  methods: {
    // getRadioValue(question, index) {
    //   let answerIndex = this.assignment.answers[index].value;
    //   let choice = question.choices[answerIndex];
    //   return choice;
    // },

    completeReview() {
      HomeworkService.ReviewAssignment(this.assignment).then(() => {
        this.$router.go();
      });
    }
  }
};
</script>

<style>
.v-textarea input {
  font-size: 2em;
}
</style>